import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AventriHomeComponent } from './pages/views/Aventri/aventri-home/aventri-home.component';

const routes: Routes = [
  { path: 'aventri-home', component: AventriHomeComponent },
  { path: '**', component: AventriHomeComponent },
  { path: '', redirectTo: 'aventri-home', pathMatch: 'full' },
];

@NgModule({
  declarations: [
    
  ],
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
